.Palace {
  .palace-header {

    .theme-header {
      background: url(https://www.almondlee.cn/storage/006tKfTcgy1g1om8efw2nj307z04a40f.jpg) no-repeat;
      background-size: 100% 100%;
      height: 15vh;
      filter: blur(4px);
      //box-shadow: 0px 9px 7px 0px rgba(130, 130, 130, 0.4);
    }

    .header-text {
      font-size: 3em;
      color: white;
      position: absolute;
      top: 10vh;
      left: 10px;
      right: 10px;
      text-align: center;

    }
  }

  .sticky {
    position: sticky;
    top: 0;
    background-color: #eeeeee;
  }

  .nav-route {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    //box-shadow: 0 0 4px rgba(0, 0, 0, 0.12);

    //border-bottom: 1px solid #e6e6e6;
    a {
      padding: 2vw;
      margin: 2vw;

    }

    .active {

    }
  }

  .tpl-select {
    padding: 1vw;
    display: flex;
    justify-content: center;
    flex-wrap: wrap-reverse;

    .ant-radio-button-wrapper {
      padding: 0 2vw;
    }
  }

  /* 容器 <div> - 需要定位下拉内容 */

  .dropdown {
    position: relative;
    display: inline-block;
    z-index: 1;
    /* 下拉按钮样式 */

    .dropbtn {
      background-color: white;
      color: black;
      padding: 16px;
      font-size: 16px;
      border: none;
      cursor: pointer;
    }
  }

  /* 下拉内容 (默认隐藏) */

  .dropdown-content {
    display: none;
    position: absolute;
    background-color: #f9f9f9;
    min-width: 30vw;
    box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  }

  /* 下拉菜单的链接 */

  .dropdown-content span {
    color: black;
    padding: 5vw;
    text-decoration: none;
    display: block;
  }

  /* 鼠标移上去后修改下拉菜单链接颜色 */

  .dropdown-content span:hover {
    background-color: #f1f1f1
  }

  /* 在鼠标移上去后显示下拉菜单 */

  .dropdown:hover .dropdown-content {
    display: block;
  }

  /* 当下拉内容显示后修改下拉按钮的背景颜色 */

  .dropdown:hover .dropbtn {
    background-color: lightgrey;
  }
}