.content-skill {
  .poster-info {
    padding: 5vw;
    border-bottom: 1px solid #d9d9d9;

    .name {
      margin-left: 4vw;
    }

    .find-him {
      //margin: 5vw;
      text-align: center;
      float: right;
    }
  }

  .skill-main {
    padding: 5vw;

    .skill-info {
      font-size: 1.8em;
    }

    .skill-img {
      margin-top: 4vw;
      //width: 100vw;
      text-align: center;
      //padding: 5vw;
      border-radius: 5%;

      img {
        width: 100%;
      }
    }

    .explain-info {
      margin: 5vw 0;
    }
    .pinlun-container{
      border-top: 1px solid #d9d9d9;

      padding: 2vw;
      text-align: left;
      .pinlun-cell{
        padding: 2vw;

        .pinlun-time{
          font-size: 0.5em;
          color: grey;
          border-bottom: 1px solid #e6e6e6;

        }

      }
    }
    .footer-skill {
      background: white;
      position: fixed;
      bottom: 0;
      right: 0;
      left: 0;
      z-index: 1;
      .message-input{
        display: flex;
        padding: 2vw;
        .toggle-down{
          padding: 2vw;
        }

      }
      .default-footer {
        display: flex;

        .icon-favorite {
          margin: 3vw;
        }

        .right {
          margin-left: auto;
          order: 3;

          button {
            color: white;
            background-color: #71ceff;
            border-color: #71ceff;
            transform: translateY(-30%);
          }
        }
      }

    }
  }
}

.Skills .sticky .tpl-select .ant-radio-button-wrapper {
  padding: 0 2vw;
  min-width: 24%;
  text-align: center;
  margin: 1px;
  border-radius: 5px;
}